<template>
    <div class="market-data-container">
        <h2 class="market-data-title">마대 시세표</h2>
        <table class="market-data-table">
            <thead>
                <tr>
                    <th>아이템명</th>
                    <th>평균 단가</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in sortedMarketData" :key="item.item">
                    <td>{{ item.item }}</td>
                    <td>{{ formatPrice(item.averagePrice) }}원</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script setup>
import { ref, onMounted, computed } from "vue";
import axios from "axios";

const sheetData = ref([]); // 원본 데이터
const processedData = ref([]); // 가공된 데이터

const sortedMarketData = computed(() => {
    return [...processedData.value].sort(
        (a, b) => b.averagePrice - a.averagePrice
    );
});

const formatPrice = (price) => {
    return new Intl.NumberFormat("ko-KR").format(price);
};

const processData = (data) => {
    const groupedData = data.reduce((acc, item) => {
        const matches = item.item.match(/\((\d+)개\)$/);
        let count = 1;
        let processedItem = item.item;

        if (matches) {
            count = parseInt(matches[1]);
            processedItem = item.item.replace(/\(\d+개\)$/, "").trim();
        }

        const price = parseInt(item.price.replace(/,/g, ""));

        if (!acc[processedItem]) {
            acc[processedItem] = {
                item: processedItem,
                totalCount: 0,
                totalPrice: 0,
                transactions: [],
            };
        }

        acc[processedItem].totalCount += count;
        acc[processedItem].totalPrice += price;
        acc[processedItem].transactions.push({
            date: item.date,
            time: item.time,
            buyer: item.buyer,
            price: price,
            count: count,
        });

        return acc;
    }, {});

    return Object.values(groupedData).map((group) => ({
        ...group,
        averagePrice: Math.round(group.totalPrice / group.totalCount),
    }));
};

const loading = ref(true);
const error = ref(null);

const fetchSheetData = async () => {
    const SHEET_ID = "1FpPV7ChZm1EaG4EfMBjTTy4qwNR-P-3qUTRQhpmAr7E";
    const SHEET_NAME = "마대시장";
    const SHEET_RANGE = "A:E";
    const API_KEY = "AIzaSyA-UjgOrvO_9OYa3L5L_Hb7q6MN2Wh1mVo"; // 여기에 본인의 Google API 키를 입력하세요

    const url = `https://sheets.googleapis.com/v4/spreadsheets/${SHEET_ID}/values/${SHEET_NAME}!${SHEET_RANGE}?key=${API_KEY}`;

    try {
        const response = await axios.get(url);
        const values = response.data.values;

        // 첫 번째 행은 헤더이므로 제외하고 데이터를 처리합니다
        sheetData.value = values.slice(1).map((row) => ({
            date: row[0],
            time: row[1],
            item: row[2],
            buyer: row[3],
            price: row[4],
        }));

        loading.value = false;
    } catch (err) {
        console.error("Error fetching sheet data:", err);
        error.value = "데이터를 불러오는 중 오류가 발생했습니다.";
        loading.value = false;
    }
};

onMounted(async () => {
    await fetchSheetData();
    processedData.value = processData(sheetData.value);
    // console.log("Sheet data:", sheetData.value);
    // console.log("Processed data:", processedData.value);
});
</script>

<style scoped>
.market-data-container {
    font-family: Arial, sans-serif;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
}

.market-data-title {
    color: #333;
    text-align: center;
    margin-bottom: 20px;
}

.market-data-table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.market-data-table th,
.market-data-table td {
    padding: 12px 15px;
    text-align: left;
}

.market-data-table thead {
    background-color: #f8f9fa;
}

.market-data-table th {
    background-color: #4a90e2;
    color: white;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 0.03em;
    border-top: 1px solid #ddd;
}

.market-data-table tr:nth-child(even) {
    background-color: #f8f9fa;
}

.market-data-table tr:hover {
    background-color: #e9ecef;
    transition: background-color 0.3s ease;
}

.market-data-table td {
    border-bottom: 1px solid #ddd;
}

.market-data-table tr:last-child td {
    border-bottom: none;
}

@media screen and (max-width: 600px) {
    .market-data-table {
        font-size: 14px;
    }

    .market-data-table th,
    .market-data-table td {
        padding: 8px 10px;
    }
}
</style>
